.top-block {
  min-height: 878px;
  font-family: $mono;
  color: #ffffff;
  background-image: url("../img/main_desktop.jpg");
  background-color: #1b2d37;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;

  @media (min-resolution: $retina-dppx) {
    background-image: url("../img/main_desktop_2x.jpg");
  }
}

.top-block a {
  color: #ffffff;

  &:hover,
  &:focus,
  &:active {
    opacity: 0.7;
  }
}

.top-block__wrapper {
  max-width: 1515px;
  padding-left: 50px;
  padding-right: 50px;
  padding-bottom: 200px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.top-block__image {
  position: absolute;
  top: 145px;
  left: 784px;
  background-image: url("../img/triangles.svg");
  background-repeat: no-repeat;
  background-position-y: 116%;
  background-position-x: -20%;
}

.top-block__info {
  position: relative;
  padding-top: 122px;
}

.top-block__info h3 {
  max-width: 720px;
  font-family: $roboto;
  font-weight: bold;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  text-transform: uppercase;
  margin-left: -4px;
  margin-bottom: 27px;
}

.top-block__info p {
  max-width: 750px;
  font-size: 22px;
  line-height: 34px;
  padding-left: 95px;
  margin-bottom: 44px;
  position: relative;

  &::before {
    content: "";
    width: 66px;
    height: 2px;
    position: absolute;
    top: 17px;
    left: 0;
    background-color: #ffffff;
  }
}

.top-block__scroll {
  width: 110px;
  transform: rotate(-90deg);
  position: absolute;
  left: 0;
  bottom: 60px;
}

.top-block__scroll a {
  font-size: 14px;
  opacity: 0.5;
}

.top-block__consult {
  width: 407px;
  height: 55px;
  background: #00b2ff;
  border-radius: 5px;
  font-family: $roboto;
  font-weight: bold;
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  margin-left: 95px;
  display: flex;
  justify-content: center;
  align-items: center;
}

